import React from 'react'
import './dropdown.scss'

const Option = props => {
  console.log(props.selectedEl)
  return (
    <div className="option"
      onClick={props.updateSelected("hello")}
    >
      <input
        type="radio"
        className="TnC__option radio"
        id={props.location} name="dropdown"
      />
      <label htmlFor={props.location}>{props.location}</label>
    </div>
  )
}

export default Option
